<template>
  <v-container>
    <v-layout text-xs-center wrap grow xs12>
      <!-- region Logo -->
      <v-flex xs12 class="pt-5">
        <svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 24 24">
          <path d="M0 0h24v24H0z" fill="none"/>
          <path
              d="M15 1H9v2h6V1zm-4 13h2V8h-2v6zm8.03-6.61l1.42-1.42c-.43-.51-.9-.99-1.41-1.41l-1.42 1.42C16.07 4.74 14.12 4 12 4c-4.97 0-9 4.03-9 9s4.02 9 9 9 9-4.03 9-9c0-2.12-.74-4.07-1.97-5.61zM12 20c-3.87 0-7-3.13-7-7s3.13-7 7-7 7 3.13 7 7-3.13 7-7 7z"/>
        </svg>
      </v-flex>
      <!-- endregion -->

      <!-- region Stopwatch Section -->
      <v-flex mb-4>
        <h1 class="display-3 font-weight-bold mb-3" v-html="formatTime(lapsedMilliseconds)"></h1>

        <v-btn v-if="state === 'reset'" @click="start" color="success" v-shortkey="['s']" @shortkey="start">
          Start
        </v-btn>

        <v-btn v-if="state === 'start'" @click="stop(); assembleFutureEntry(); neuerTeilnehmerDialog = true" color="error" v-shortkey="['p']" @shortkey="stop(); assembleFutureEntry(); neuerTeilnehmerDialog = true">
          Stop
        </v-btn>

<!--
        <v-btn v-if="state === 'reset' && rows.length >= rowMax" @click="neueRundeModal = true" color="warning">
          Neue Runde
        </v-btn>
-->
      </v-flex>
      <!-- endregion -->

      <v-flex xs12 class="pt-5">
        <v-btn @click="openList = true" color="warning">
          Top {{ rowMax }} Liste
        </v-btn>
      </v-flex>

      <!-- region Top 10 Section -->
      <WindowPortal v-model="openList">
        <v-layout text-xs-center wrap grow xs12 class="roboto-font">
          <v-flex xs12>
            <h2 class="headline font-weight-bold mb-3 pt-4" >Top {{ rowMax }}</h2>
            <v-layout class="justify-center">
              <template>
                <v-data-table
                    :hide-actions="true"
                    :headers="rowHeaders"
                    :items="rows"
                    :rows-per-page-items="itemsPerPage"
                    :rows-per-page.sync="defaultRowsPerPage"
                    class="elevation-1"
                    no-data-text="Keine Einträge vorhanden."
                >
                  <template v-slot:items="props">
                    <td class="text-xs-left">{{ props.item.stelle }}</td>
                    <td class="text-xs-left">{{ formatTime(props.item.zeit) }}</td>
                    <td class="text-xs-left">{{ props.item.teilnehmer }}</td>
                    <td class="justify-center layout px-0">
                      <v-icon @click="
                        removeEntryModalStelle = props.item.stelle;
                        removeEntryModalTeilnehmer = props.item.teilnehmer;
                        removeEntryModalElement = props.item;
                        removeEntryModal = true"
                      >delete</v-icon>
                    </td>
                  </template>
                </v-data-table>
              </template>
            </v-layout>
          </v-flex>
        </v-layout>
      </WindowPortal>
      <!-- endregion -->

      <!-- region Teilnehmer löschen Dialog -->
      <v-dialog v-model="removeEntryModal" persistent max-width="600px">
        <v-card>
          <v-card-title class="headline">Position {{ removeEntryModalStelle }} löschen</v-card-title>
          <v-card-text>Wirklich den Eintrag von {{ removeEntryModalTeilnehmer }} löschen?
          </v-card-text>
          <v-card-actions>
            <v-btn color="red" flat @click="removeEntryModal = false">Nein</v-btn>
            <v-spacer></v-spacer>
            <v-btn color="green" flat @click="removeEntryModal = false; removeEntry(removeEntryModalElement)">Ja</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <!-- endregion -->

      <!-- region Neuer Eintrag in die Top 10 -->
      <v-dialog v-model="neuerTeilnehmerDialog" persistent max-width="600px">

        <v-card v-if="futureEntry === undefined || futureEntry === null">
          <v-card-title v-if="futureEntry === undefined || futureEntry === null" class="justify-center">
            <span class="headline">Du hast leider keinen Platz geschafft !</span>
          </v-card-title>
          <v-card-actions class="justify-center">
            <v-btn color="success" @click="neuerTeilnehmerDialog = false; neuerTeilnehmer = ''; futureEntry = null; reset()">Ok, schade</v-btn>
          </v-card-actions>
        </v-card>

        <v-card v-if="futureEntry !== undefined && futureEntry !== null">
          <v-card-title class="justify-center">
            <span class="headline">Du bist die Nummer {{ futureEntry.stelle }} !</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field v-if="neuerTeilnehmerDialog" autofocus label="Dein Name" @keyup.enter="neuerTeilnehmerDialog = false; saveEntry(); neuerTeilnehmer = ''; futureEntry = null; reset()" v-model="neuerTeilnehmer" required></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-btn color="error" @click="neuerTeilnehmerDialog = false; neuerTeilnehmer = ''; futureEntry = null; reset()">Abbrechen</v-btn>
            <v-spacer></v-spacer>
            <v-btn color="success" @click="neuerTeilnehmerDialog = false; saveEntry(); neuerTeilnehmer = ''; futureEntry = null; reset()">Fertig</v-btn>
          </v-card-actions>
        </v-card>

      </v-dialog>
      <!-- endregion -->

      <!-- region Settings Dialog -->
      <v-dialog v-model="einstellungenDialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">Einstellungen</span>
            <v-spacer></v-spacer>
            <v-btn @click="einstellungenDialog = false" flat icon>
              <v-icon>close</v-icon>
            </v-btn>
          </v-card-title>
          <v-card-actions>
            <v-btn color="error" class="d-flex justify-center" flat @click="discardStoredInstanceModal = true">Alle Daten löschen!</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <!-- endregion -->

      <!-- region Help-Dialog -->
      <v-dialog v-model="helpDialog" max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">Hilfe</span>
            <v-spacer></v-spacer>
            <v-btn @click="helpDialog = false" icon>
              <v-icon>close</v-icon>
            </v-btn>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <h3>Tastaturbefehle</h3>
                  Start
                  <v-chip>s</v-chip>
                  <br/>
                  <br/>
                  Stop:
                  <v-chip>p</v-chip>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
        </v-card>
      </v-dialog>
      <!-- endregion -->

      <!-- region Alle Daten löschen Dialog -->
      <v-dialog v-model="neueRundeModal" persistent max-width="600px">
        <v-card>
          <v-card-title class="headline">Eine neue Runde beginnen</v-card-title>
          <v-card-text>Wirklich alle Einträge in der Tabelle löschen und eine neue Runde beginnen?
          </v-card-text>
          <v-card-actions>
            <v-btn color="error" flat @click="neueRundeModal = false;">Nein</v-btn>
            <v-spacer></v-spacer>
            <v-btn color="success" flat @click="neueRundeModal = false; neueRunde()">Ja</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <!-- endregion -->

      <!-- region Alle Daten löschen Dialog -->
      <v-dialog v-model="discardStoredInstanceModal" persistent max-width="600px">
        <v-card>
          <v-card-title class="headline">Alle Daten löschen</v-card-title>
          <v-card-text>Wirklich alle gespeicherten Top 10 Daten löschen?
          </v-card-text>
          <v-card-actions>
            <v-btn color="red" flat @click="discardStoredInstanceModal = false; einstellungenDialog = false;">Nein</v-btn>
            <v-spacer></v-spacer>
            <v-btn color="green" flat @click="discardStoredInstanceModal = false; discardStoredInstance()">Ja</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <!-- endregion -->

      <!-- region Settings deleted snackbar -->
      <v-snackbar v-model="discardStoredInstanceSnackbar" bottom :timeout="20000">
        Alle Daten wurden gelöscht, Browserfenster bitte neu laden...
        <v-btn color="blue" flat @click="discardStoredInstanceSnackbar = false">
          Close
        </v-btn>
      </v-snackbar>
      <!-- endregion -->

    </v-layout>
  </v-container>
</template>

<script>
import props from "vuetify/lib/components/VCalendar/util/props";
import WindowPortal from './WindowPortal';

export default {
  components: {
    WindowPortal
  },
  data: () => ({
    // region general stuff
    openList: false,
    state: 'reset',
    currentTime: Date.now(),
    startTime: Date.now(),
    stopTime: 0,
    interval: null,
    intervalUpdateTime: 10,
    // endregion

    // region intervals
    rows: [],
    rowCount: 0,
    rowMax: 10,
    futureEntry: {stelle: 0, zeit: '', teilnehmer: ''},
    rowHeaders: [
      {text: 'Position', value: 'stelle', sortable: false},
      {text: 'Zeit', value: 'zeit', sortable: false},
      {text: 'Teilnehmer', value: 'teilnehmer', sortable: false},
      {text: 'Entfernen', value: 'stelle', sortable: false}
    ],
    itemsPerPage: [10],  // This sets the choices for `rows per page`
    defaultRowsPerPage: 10,
    // endregion

    // region settings
    millisecondsDigitAmount: 1,
    millisecondsSeparator: ':',
    // endregion

    // region dialogs
    einstellungenDialog: false,
    helpDialog: false,
    removeEntryModal: false,
    removeEntryModalStelle: '',
    removeEntryModalTeilnehmer: '',
    removeEntryModalElement: null,

    neuerTeilnehmerDialog: false,
    neuerTeilnehmer: '',

    discardStoredInstanceModal: false,
    discardStoredInstanceSnackbar: false,
    neueRundeModal: false,
    // endregion
  }),
  mounted() {
    this.checkForInstance();
  },
  destroyed() {
    this.stopUpdateInterval();
  },
  computed: {
    props() {
      return props
    },
    lapsedMilliseconds() {
      return this.currentTime - this.startTime - this.stopTime;
    },
    shortKeyStop() {
      return this.state === 'start' ? ['p'] : [];
    }
  },
  methods: {
    // region Time Formatting
    formatTime(lapsed) {
      if (lapsed === undefined || lapsed === null) {
        lapsed = this.lapsedMilliseconds;
      }

      return this.formatHours(lapsed) +
          ':' + this.formatMinutes(lapsed) +
          ':' + this.formatSeconds(lapsed) +
          this.millisecondsSeparator +
          this.formatMilliseconds(lapsed).substring(0, this.millisecondsDigitAmount);
    },
    formatHours(lapsed) {
      const hrs = Math.floor((lapsed / 1000 / 60 / 60));
      return hrs >= 10 ? hrs : '0' + hrs;
    },
    formatMinutes(lapsed) {
      const min = Math.floor((lapsed / 1000 / 60) % 60);
      return min >= 10 ? min : '0' + min;
    },
    formatSeconds(lapsed) {
      const sec = Math.floor((lapsed / 1000) % 60);
      return sec >= 10 ? sec : '0' + sec;
    },
    formatMilliseconds(lapsed) {
      const mil = Math.ceil(lapsed % 1000);

      if (mil < 10) {
        return '00' + mil;
      } else if (mil < 100) {
        return '0' + mil;
      } else {
        return '' + mil;
      }
    },
    // endregion

    // region Stopwatch Actions
    start() {
      this.startTime = Date.now();
      this.state = 'start';
      this.startUpdateInterval();
      this.storeInstance();
    },

    reset: function () {
      this.stopUpdateInterval();
      this.state = "reset";
      this.currentTime = Date.now();
      this.startTime = Date.now();
      this.stopTime = 0;
      this.storeInstance();
    },

    stop: function () {
      this.stopUpdateInterval();
      this.state = "stop";
      this.storeInstance();
    },

    updateCurrentTime() {
      this.currentTime = Date.now();
    },
    // endregion

    // region Intervals
    /** Start updating of current time */
    startUpdateInterval() {
      this.interval = setInterval(this.updateCurrentTime, this.intervalUpdateTime);
    },

    /** Stop updating of current time */
    stopUpdateInterval() {
      clearInterval(this.interval);
    },

    /** Save new interval (with current time) */
    saveEntry() {
      let rows = JSON.parse(JSON.stringify(this.rows));

      let zeit = this.lapsedMilliseconds;
      rows.push({
        stelle: this.rowCount,
        zeit: zeit,
        teilnehmer: this.neuerTeilnehmer,
      });

      rows = this.sortRowsByZeitAndUpdateStelle(rows);
      rows = rows.slice(0, this.rowMax);
      this.rows = rows;
      this.rowCount = this.rows.length;

      this.storeInstance();
    },

    assembleFutureEntry() {
      let futureRows = JSON.parse(JSON.stringify(this.rows));
      let futureRowCount = futureRows.length;

      let zeit = this.lapsedMilliseconds;
      futureRows.push({
        stelle: futureRowCount,
        zeit: zeit,
        teilnehmer: this.neuerTeilnehmer,
      });

      futureRows = this.sortRowsByZeitAndUpdateStelle(futureRows);
      futureRows = futureRows.slice(0, this.rowMax);

      futureRowCount = futureRows.length;
      this.futureEntry = futureRows.find(item => item.zeit === this.lapsedMilliseconds);
    },

    /** Sort rows by Zeit and update Stelle jeweils */
    sortRowsByZeitAndUpdateStelle(array) {
      let arrayNew = JSON.parse(JSON.stringify(array));

      arrayNew.sort((a, b) => a.zeit - b.zeit);
      arrayNew.forEach((item, index) => {
        item.stelle = index + 1;
      });

      return arrayNew;
    },

    /** Remove specific time from intervals */
    removeEntry(element) {
      const index = this.rows.indexOf(element);
      let rows = JSON.parse(JSON.stringify(this.rows));
      rows.splice(index, 1);
      rows = this.sortRowsByZeitAndUpdateStelle(rows);
      this.rows = rows;
      this.rowCount = this.rows.length;
      this.storeInstance();
    },
    // endregion

    // region Stored Instance
    /**
     * Store the current state of the instance.
     */
    storeInstance() {
      localStorage.setItem('instanceCurrentTime', this.currentTime);
      localStorage.setItem('instanceStartTime', this.startTime);
      localStorage.setItem('instanceStopTime', this.stopTime);
      localStorage.setItem('instanceState', this.state);
      localStorage.setItem('instanceRows', JSON.stringify(this.rows));
      localStorage.setItem('instanceRowCount', this.rowCount);
    },
    /**
     * Check whether an instance had been start but not reset manually, that is usually the
     * case when the browser/ system crashes or the user accidentally closed the browser (tab).
     */
    checkForInstance() {
      if (localStorage.getItem('instanceStartTime') !== null) {
        this.restoreStoredInstance();
      }
    },
    /**
     * Restore the stored instance and start the stopwatch with the old parameters again.
     */
    restoreStoredInstance() {
      const currentTime = localStorage.getItem('instanceCurrentTime');
      const startTime = localStorage.getItem('instanceStartTime');
      const stopTime = localStorage.getItem('instanceStopTime');
      const rowCount = localStorage.getItem('instanceRowCount');
      const rows = localStorage.getItem('instanceRows');
      const state = localStorage.getItem('instanceState');

      if (currentTime !== null) {
        this.currentTime = parseInt(currentTime);
      }

      if (startTime !== null) {
        this.startTime = parseInt(startTime);
      }

      if (stopTime!== null) {
        this.stopTime = parseInt(stopTime);
      }

      if (rowCount !== null) {
        this.rowCount = parseInt(rowCount);
      }

      if (rows !== null && rows !== undefined) {
        this.rows = JSON.parse(rows);
      }

      if (state!== null) {
        this.state = state;
      }

      if (this.state === 'start') {
        this.startUpdateInterval();
      }
    },
    /**
     * Delete the stored instance.
     */
    discardStoredInstance() {
      localStorage.removeItem('instanceStartTime');
      localStorage.removeItem('instanceRows');
      localStorage.removeItem('instanceRowCount');
      localStorage.removeItem('instanceStopTime');
      localStorage.removeItem('instanceState');
      this.einstellungenDialog = false;
      this.discardStoredInstanceSnackbar = true;
    },

    neueRunde() {
      this.rows = [];
      this.rowCount = 0;
      this.storeInstance();
    },
    // endregion

    // region Dialogs
    openHelpDialog() {
      this.helpDialog = true;
    },

    openSettingsDialog() {
      this.einstellungenDialog = true;
    },
    // endregion

    // region helper functions
    /**
     * Swap two elements in an array.
     * (Helper function)
     */
    swapArray(array, l, m) {
      let a = array[l];
      let b = array[m];

      // Use Vue.set() to prevent re-loading/ binding problems in v-for
      this.$set(array, l, b);
      this.$set(array, m, a);
    },
    // endregion
  }
}
</script>

<style>
.roboto-font {
  font-family: 'Roboto', sans-serif;
}

.v-label {
  font-size: 80%;
}

.v-input {
  font-size: 150%;
}

.v-card__text {
  font-size: 150%;
}

.v-btn {
  font-size: 150%;
}

.headline {
  font-size: 200% !important;
}

table.v-table thead th {
  font-size: 150%;
}

table.v-table tbody td {
  font-weight: 500;
  font-size: 150%;
}
</style>
